import { CCFloat } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";

const { ccclass, property } = _decorator;
@ccclass('pbCardNum')
export class pbCardNum extends Component {
    @property({ type: CCFloat, tooltip: "showNodeNum" })
    public showNodeNum: number = 1;
    @property({ type: CCFloat, tooltip: "val" })
    public val: number = 0;
}