import { _decorator, Component, Node } from 'cc';
import { rabbit } from './cRabbit';
import { Label } from 'cc';
import { pbCardNum } from './pbCardNum';
const { ccclass, property } = _decorator;

const GAME_COMBINATIONS : any[] = [
    { 3: 2 },		// hand cards no fix line 0
    { 3: 3 },		// hand cards no fix line 1
    { 3: 5 },		// hand cards no fix line 2
    { 3: 10 },		// hand cards no fix line 3
    { 3: 50 },	    // hand cards no fix line 4
    { 3: 100 },	    // hand cards no fix line 5
    { 3: 200 },	    // hand cards no fix line 6
    { 3: 0 },	    // hand cards no fix line 7
];
@ccclass('rabbitDetail')
export class rabbitDetail extends Component {
    @property({ type: Node, tooltip: "图片" })
    public roleSpriteNode: Node = null!;
    @property({ type: Node, tooltip: "背景板" })
    public BGNode: Node = null!;
    @property({ type: Node, tooltip: "反向背景板" })
    public BGNode2: Node = null!;
    @property({ type: Node, tooltip: "中奖分数" })
    public numNode: Node = null!;
    @property({ type: Node, tooltip: "特殊牌" })
    public specialNode: Node = null!;
    @property({ type: Node, tooltip: "其他牌" })
    public otherNode: Node = null!;

    init(idx: number, isLeft: boolean, target:Node|undefined = undefined) {
        if (rabbit.detail) {
            rabbit.detail.node.active = false;
        }
        rabbit.detail = this;

        let id = idx;

        this.roleSpriteNode.children[idx - 1].active = true;

        if (idx == 8) {
            this.BGNode.w = 685
            this.BGNode2.w = 685
            this.otherNode.active = true
            this.numNode.active = false
            let nd = this.roleSpriteNode.children[idx - 1]
            let cardNumItem:pbCardNum = (target as Node).getComponent(pbCardNum) as pbCardNum;
            (nd.getChildByName("001") as Node).active = false;
            (nd.getChildByName("002") as Node).active = false;
            (nd.getChildByName("003") as Node).active = false;
            let labNode:Node = nd.getChildByName("00"+cardNumItem.showNodeNum) as Node;
            labNode.active = true;
            (labNode.getComponent(Label) as Label).string = cardNumItem.val+"";
        } else {
            this.BGNode.w = 542
            this.BGNode2.w = 542
            this.otherNode.active = false
            this.numNode.active = true
            for (let i in GAME_COMBINATIONS[id - 1]) {
                ((((this.numNode as Node).getChildByName(i) as Node).children[0] as Node).getComponent(Label) as Label).string = GAME_COMBINATIONS[idx - 1][i];
            }
        }

        if (idx == 7) {
            this.BGNode.h = 367
            this.BGNode2.h = 367
        } else {
            this.BGNode.h = 281
            this.BGNode2.h = 281
        }

        //位于右边的时候需要切换显示位置
        if (!isLeft) {
            this.BGNode.active = false;
            this.BGNode2.active = true;
            this.numNode.x = -180;
            this.specialNode.x = -340;
            this.otherNode.x = -430;
        }
    }

    node_onclick(e: any) {
        rabbit.node.deskMask.active = false;
        this.node.destroy();
        rabbit.detail = null;
    }

}