import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
const { ccclass, property } = _decorator;

@ccclass('rabbitAudio')
export class rabbitAudio extends Component {
    @property({ type: [AudioClip], tooltip: "bgmAudio" })
    public bgmAudio = [];
    @property({ type: AudioClip, tooltip: "滚轮旋转" })
    public wheelspin = null!;
    @property({ type: AudioClip, tooltip: "滚轮停止" })
    public stopWheel = null!;
    @property({ type: AudioClip, tooltip: "粒子触发" })
    public particalStart = null!;
    @property({ type: AudioClip, tooltip: "粒子结束" })
    public particalEnd = null!;
    @property({ type: AudioClip, tooltip: "语音_1_兔来运转" })
    public rabbit_01 = null!;
    @property({ type: AudioClip, tooltip: "语音_2_福到财到" })
    public rabbit_02 = null!;
    @property({ type: AudioClip, tooltip: "语音_3_啊哈啊哈" })
    public rabbit_03 = null!;
    @property({ type: AudioClip, tooltip: "语音_4_嗯哼" })
    public rabbit_04 = null!;
    @property({ type: AudioClip, tooltip: "语音_5_恭喜恭喜" })
    public rabbit_05 = null!;
    @property({ type: AudioClip, tooltip: "语音_6_厉害" })
    public rabbit_06 = null!;
    @property({ type: AudioClip, tooltip: "语音_7_真好" })
    public rabbit_07 = null!;
    @property({ type: AudioClip, tooltip: "语音_8_啊哈" })
    public rabbit_08 = null!;
    @property({ type: AudioClip, tooltip: "语音_9_嘿嘿" })
    public rabbit_09 = null!;
    @property({ type: AudioClip, tooltip: "语音_10_好" })
    public rabbit_10 = null!;
    @property({ type: AudioClip, tooltip: "语音_11_耶" })
    public rabbit_11 = null!;
    @property({ type: AudioClip, tooltip: "bigWinEf" })
    public bigWinEf = null!;
    @property({ type: AudioClip, tooltip: "中奖首次音效" })
    public WinFirstEf = null!;
    @property({ type: AudioClip, tooltip: "中奖循环音效" })
    public WinLoopEf = null!;
    @property({ type: AudioClip, tooltip: "点击旋转" })
    public clickstart = null!;
    @property({ type: AudioClip, tooltip: "点击打开" })
    public clickOpen = null!;
    @property({ type: AudioClip, tooltip: "点击关闭" })
    public clickClose = null!;

    onLoad() {

    }

    start() {
        this.playBgm();
    }

    stopAudio() {
        // AudioMgr.inst.stop();
    }

    stopbigwinmain() {
        this.playBgm();
    }

    private url:string = "sound/bgm";
    playBgm() {
        // AudioMgr.inst.stop();
        // AudioMgr.inst.playMusicLoop(this.url,EGameName.rabbit)
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_RABBIT)
    }

    private BWurl:string = "sound/bigwin";
    playbigwinmain() {
        // AudioMgr.inst.playMusic(this.BWurl,EGameName.rabbit);
    }

    playStopWheel() {
        this.playEf(this.stopWheel);
    }

    playwheelspin() {
        this.playEf(this.wheelspin);
    }

    playParticalStart() {
        this.playEf(this.particalStart);
    }

    playclickstart() {
        this.playEf(this.clickstart);
    }

    playParticalEnd() {
        this.playEf(this.particalEnd);
    }

    playrabbit_01() {
        this.playEf(this.rabbit_01);
    }

    playrabbit_02() {
        this.playEf(this.rabbit_02);
    }

    playBWCome() {
        let idx = Math.floor(Math.random());
        if (idx == 0) {
            this.playEf(this.rabbit_03);//语音_3_啊哈啊哈 
        } else {
            this.playEf(this.rabbit_04);//语音_4_嗯哼 
        }
    }

    playWinLow() {
        let auList = [this.rabbit_08, this.rabbit_09, this.rabbit_10, this.rabbit_11];
        let idx = Math.floor(Math.random() * auList.length);
        this.playEf(auList[idx]);
    }

    playWinHigh() {
        let auList = [this.rabbit_05, this.rabbit_06, this.rabbit_07];
        let idx = Math.floor(Math.random() * auList.length);
        this.playEf(auList[idx]);
    }

    playWinFirst() {
        this.playEf(this.WinFirstEf);
    }

    playWinLoop() {
        this.playEf(this.WinLoopEf);
    }

    playBW() {
        this.playEf(this.bigWinEf);
    }

    playEf(clip: AudioClip) {
        // AudioMgr.inst.playEffect(clip);
        tgx.AudioMgr.inst.audio.playEffect(clip);
    }

    playclickopen() {
        
    }

    playclickclose() {

    }

    clearAll(){
        tgx.AudioMgr.inst.audio.clearAll();
    }

}