import { rabbitMain } from "./rabbitMain";
import { rabbitDetail } from "./rabbitDetail";
import { rabbitWheel } from "./rabbitWheel";
import { rabbitSlotCtrl } from "./rabbitSlotCtrl";
import { betPanel } from "./betPanel";
import { autoPanel } from "./autoPanel";
import { lastmoneyPanel } from "./lastmoneyPanel";
import { rulePanel } from "./rulePanel";
import { payPanel } from "./payPanel";
import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { dataChange } from "./dataChange";

class cRabbit {
    public static _instance: cRabbit | null = null;

    public static get instance(): cRabbit {
        if (!this._instance) {
            this._instance = new cRabbit();
        }
        return this._instance;
    }

    public BETNUM = dataChange.BETNUM;
    public LINES = dataChange.LINES;
    public BET = dataChange.BET;

    private _isNewUser: boolean = false;
    public get isNewUser(): boolean {
        this._isNewUser = false;
        return this._isNewUser;
    }

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    public detail: rabbitDetail | null = null;
    public node: rabbitMain = null!;
    public get slotCtrl(): rabbitSlotCtrl {
        return this.node.slotCtrl;
    }

    public wheelList: rabbitWheel[] = [];
    public bet: number = 0;
    public betNum: number = 0;
    public sumIdx: number = 0;

    /**累计下注计算 */
    private _betSum: number = 0;
    public get betSum(): number {
        if (!this._betSum) {
            this._betSum = Number(localStorage.getItem(SubGameDef.SLOTS_RABBIT + "betSum"));
        }
        return this._betSum;
    }
    public set betSum(v: number) {
        if (this._betSum != v) {
            localStorage.setItem(SubGameDef.SLOTS_RABBIT + "betSum", v + "");
        }
        this._betSum = v;
    }

    /**获奖信息 */
    public lotteryRes: any = null!;

    /**自动状态 */
    public auto: boolean = false;
    /**自动次数 */
    public autoTimes: number = 0;
    /**转动状态0：停止，1：转动，2：开始转 */
    public status: number = 0;
    /**中奖数字位置 */
    public winLinePos: number[] = [];
    /**大奖个数 */
    public bigWinNum: number = 0;
    /**摇奖计数 */
    public rollIndex: number = 0;

    public _rollEnable: boolean = true;

    /**下注界面 */
    public betPanel: betPanel = null!;
    /**自动界面 */
    public autoPanel: autoPanel = null!;
    /**规则界面 */
    public rulePanel: rulePanel = null!;
    /**赔付表界面 */
    public payPanel: payPanel = null!;
    /**余额界面 */
    public lastmoneyPanel: lastmoneyPanel = null!;

    public set rollEnable(v: boolean) {
        this._rollEnable = v;
    }

    public get rollEnable(): boolean {
        return this._rollEnable;
    }

    constructor() {

    }


    public get inBigWin(): boolean {
        return this.lotteryRes.getBigWin.res_list.length > 1;
    }

    public get bigWinBn(): boolean {
        return (rabbit.lotteryRes.winscore > rabbit.betSum * 20);
    }
}

export let rabbit = cRabbit.instance;