import { Vec3 } from "cc";
import { CCFloat } from "cc";
import { tween } from "cc";
import { Component, _decorator } from "cc";

const { ccclass, property } = _decorator;
// 视图层实体是空的
@ccclass('nodeSizeJump')
export class nodeSizeJump extends Component {
    @property({ type: CCFloat, tooltip: "scale" })
    public scale: number = 0;
    @property({ type: CCFloat, tooltip: "时间间隔" })
    public dis: number = 0;
  
    play(){
        tween(this.node)
            .to(this.dis,{scale:new Vec3(this.scale,this.scale)})
            .to(this.dis,{scale:new Vec3(1,1)})
            .start();
    }
}
