    @property({ type: Node, tooltip: "免费按钮" })
    public Btn_free: Node = null!;
    @property({ type: Node, tooltip: "加注" })
    public Btn_add: Node = null!;
    @property({ type: Node, tooltip: "减注" })
    public Btn_sub: Node = null!;
    @property({ type: Node, tooltip: "自动面板" })
    public Btn_auto: Node = null!;
    @property({ type: Node, tooltip: "设置" })
    public Btn_set: Node = null!;
