import { _decorator, Component } from 'cc';
import { rabbit } from './cRabbit';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { TweenAction } from 'cc';
import { Tween } from 'cc';
import { instantiate } from 'cc';
import { pbCardNum } from './pbCardNum';
import { Label } from 'cc';
import { Node } from 'cc';
import { Button } from 'cc';
import { UITransform } from 'cc';
import { rabbitDetail } from './rabbitDetail';
import { Num } from '../../core_tgx/framework/util/Num';
import { CCFloat } from 'cc';
const { ccclass, property } = _decorator;

const ROLENUM = 20; //每一轮的角色数量
const TIMEMIN = 1.2; //第一轮摇奖时间
const FIRSTROLE = [//进入游戏时每列的角色
    [1, 6, 6, 7, 1],
    [8, 8, 8, 8, 8],
    [1, 6, 6, 7, 1],
];
@ccclass('rabbitWheel')
export class rabbitWheel extends Component {
    @property
    public wheelId: number = 0;
    @property({ type: CCFloat, tooltip: "不会出现的数字id" })
    public excludeId: number = 0;

    /**0停止 1转 */
    public status: number = 0;
    private nextCb: any;
    public rolePbList: any[] = [];
    public roleIdList: any[] = [];
    public lastResult: any[] = [];
    public lastResultNum: any[] = [];
    public p: number = 0;
    onLoad() {
        rabbit.wheelList[this.wheelId] = this;
        this.status = 0; //0停止 1转  
        this.lastResult = [0, 0, 0, 0, 0]; //中间三位是上一局结果 首位末尾是为了防止露出部分转起来不一样  
        this.nextCb = null;//下一步需要执行的回调 
        this.rolePbList = []; //roles 
        this.roleIdList = []; //role ID 
        this.lastResultNum = [0, 0, 0, 0, 0];
    }

    start() {
    }

    initWheel() {
        if (rabbit.isNewUser) {
            this.lastResult = FIRSTROLE[this.wheelId]; //中间三位是上一局结果 首位末尾是为了防止露出部分转起来不一样  
        } else {
            this.lastResult = [0, 0, 0, 0, 0];
        }
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        for (let i in this.lastResult) {
            if (rabbit.isNewUser) {
                if (this.lastResult[i] == 8) {
                    let list = [0, 2500, 100, 25, 2.5]
                    this.initAddRole(this.lastResult[i], list[i]);
                } else {
                    this.addRole(this.lastResult[i]);
                }
            } else {
                this.lastResult[i] = this.getRandomId();
                this.addRole(this.lastResult[i]);
            }
        }
        this.p = 0;
        if (this.wheelId == 1) {
            this.p = 170;
        }
        this.node.y = this.p;
        rabbit.rollIndex++;
        rabbit.node.closeShine();
        this.lastResult = this.roleIdList.slice(-5);
    }

    startRoll(...args: any) {
        for (let i = 0; i < args.length; i++) {
            if (args[i] == 9) {
                this.addRole(this.getRandomId(), false, 0);
            } else if (args[i] == 8) {
                let list = rabbit.lotteryRes.goldList;
                this.addRole(args[i], list[this.wheelId + 3 * (3 - i)], 0);
            } else {
                this.addRole(args[i], false, 0);
            }
        }
        this.addRole(this.getRandomId(), false, 0);
        let cb = () => {
            this.setBlur(false);
            if (!rabbit.slotCtrl.isSpeed) {
                tween(this.node)
                    .to(0.3, { position: new Vec3(this.node.x, -this.node.h + 660 + this.p) })
                    .to(0.1, { position: new Vec3(this.node.x, -this.node.h + 692 + this.p) })
                    .call(() => {
                        this.rollCallBack()
                    })
                    .start();
            } else {
                tween(this.node)
                    .to(0.1, { position: new Vec3(this.node.x, -this.node.h + 692 + this.p) })
                    .call(() => {
                        this.stopImmediately()
                        this.rollCallBack()
                    })
                    .start();
            }
        };
        this.nextCb = cb;
    }

    startFreeRoll(...args: any) {
        this.status = 1;
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        this.node.y = this.p; //滚轮启动位移距离 
        this.addRole(this.getRandomId());
        for (let i = args.length - 1; i >= 0; i--) {
            if (args[i] == 9) {
                this.addRole(this.getRandomId());
            } else if (args[i] == 8) {
                let list = rabbit.lotteryRes.getBigWin.res_list[rabbit.rollIndex].goldList;
                this.addRole(args[i], list[this.wheelId + 3 * (3 - i)]);
            } else {
                this.addRole(args[i]);
            }
        }
        let mul = 6;
        for (let i = 0; i < ROLENUM * 2; i++) {
            this.addRole(10);
        }
        for (let i = 0; i < ROLENUM * 4; i++) {
            this.addRole(this.getRandomId());
        }

        for (let i = 0; i < this.lastResult.length; i++) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            if (this.lastResult[i] != 0 && this.lastResultNum[i] != 0) {
                this.addRole(this.lastResult[i], this.lastResultNum[i] / parseFloat(Num.fixNum(rabbit.exchangeRate, rabbit.betSum)));
            } else {
                this.addRole(this.lastResult[i]);
            }
        }
        setTimeout(() => {
            Tween.stopAllByTarget(this.node);
            let timer = TIMEMIN * mul * 1.4 + this.wheelId * 0.2;
            tween(this.node)
                .to(timer, { position: new Vec3(this.node.x, -this.node.h + 692 + this.p) })
                .call(() => {
                    this.setBlur(false);
                })
                .to(0.3, { position: new Vec3(this.node.x, -this.node.h + 660 + this.p) })
                .to(0.1, { position: new Vec3(this.node.x, -this.node.h + 692 + this.p) })
                .call(() => {
                    this.rollCallBack()
                })
                .start();
            let temp = tween(this.node)
                .delay(0.1 + timer / 0.1)
                .call(() => {
                    rabbit.node.audio.playwheelspin();
                })
                .union()
                .repeatForever();
            temp.start();
        }, 100);
    }

    FreeRoll(...args: any) {
        this.status = 1;
        this.rolePbList = [];
        this.roleIdList = [];
        Tween.stopAllByTarget(this.node);
        this.node.removeAllChildren();
        this.node.y = this.p; //滚轮启动位移距离 
        this.addRole(10);
        for (let i = args.length - 1; i >= 0; i--) {
            if (args[i] == 9) {
                this.addRole(10);
            } else if (args[i] == 8) {
                let list = rabbit.lotteryRes.getBigWin.res_list[rabbit.rollIndex].goldList;
                this.addRole(args[i], list[this.wheelId + 3 * (3 - i)]);
            } else {
                this.addRole(args[i]);
            }
        }
        for (let i = 0; i < ROLENUM; i++) {
            let m = [0.5, 0.5, 0.5, 0.5, 0.5, 1, 1, 1, 1, 1, 5, 5, 5, 5, 5, 10, 10, 10, 10, 10, 50, 50, 50, 100, 100, 500];
            let a = Math.floor(Math.random() * m.length);
            let b = Math.floor(Math.random() * 4);
            if (b == 0) {
                this.addRole(8, a);
            } else {
                this.addRole(10);
            }
        }
        for (let i = 0; i < this.lastResult.length; i++) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            if (this.lastResult[i] != 0 && this.lastResultNum[i] != 0) {
                this.addRole(this.lastResult[i], this.lastResultNum[i] / parseFloat(Num.fixNum(rabbit.exchangeRate, rabbit.betSum)));
            } else {
                this.addRole(this.lastResult[i]);
                // this.addRole(10);
            }
        }
        setTimeout(() => {
            Tween.stopAllByTarget(this.node);
            let timer = TIMEMIN * 1.5 + this.wheelId * 0.2;
            tween(this.node)
                .delay(0.2)
                .to(timer, { position: new Vec3(this.node.x, -this.node.h + 692 + this.p) })
                .call(() => {
                    this.setBlur(false);
                })
                .to(0.1, { position: new Vec3(this.node.x, -this.node.h + 692 + this.p) })
                .call(() => {
                    this.rollCallBack()
                })
                .start();
            let temp = tween(this.node)
                .delay(0.1 + timer / 0.1)
                .call(() => {
                    rabbit.node.audio.playwheelspin();
                })
                .union()
                .repeatForever();
            temp.start();
        }, 100);
    }

    rollLoop() {
        this.status = 1;
        this.rolePbList = [];
        this.roleIdList = [];
        this.node.removeAllChildren();
        this.node.y = this.p; //滚轮启动位移距离 
        for (let i = 0; i < this.lastResult.length; i++) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            if (this.lastResult[i] != 0 && this.lastResultNum[i] != 0) {
                this.addRole(this.lastResult[i], this.lastResultNum[i] / parseFloat(Num.fixNum(rabbit.exchangeRate, rabbit.betSum)));
            } else {
                this.addRole(this.lastResult[i]);
            }
        }
        for (let i = 0; i < ROLENUM - 6; i++) {
            this.addRole(this.getRandomId());
        }
        for (let i = 0; i < this.lastResult.length; i++) {
            this.lastResult[i] = this.lastResult[i] == 0 ? this.getRandomId() : this.lastResult[i];
            if (this.lastResult[i] != 0 && this.lastResultNum[i] != 0) {
                this.addRole(this.lastResult[i], this.lastResultNum[i] / parseFloat(Num.fixNum(rabbit.exchangeRate, rabbit.betSum)));
            } else {
                this.addRole(this.lastResult[i]);
            }
        }
        let loopCb = () => {
            this.node.y = this.p; //滚轮启动位移距离 
            Tween.stopAllByTarget(this.node);
            let timer = TIMEMIN + this.wheelId * 0.2;
            this.setBlur(true);
            if (rabbit.slotCtrl.isSpeed) {
                tween(this.node)
                    .call(() => {
                        this.setBlur(true);
                    })
                    .to(timer / 3, { y: -this.node.h + 692 + this.p })//, { easing: 'quadInOut' }) 
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            } else {
                tween(this.node)
                    .call(() => {
                        this.setBlur(true);
                    })
                    .to(timer, {
                         y: -this.node.h + 692 + this.p 
                        })//, { easing: 'quadInOut' }) 
                    .call(() => {
                        this.nextCb()
                    })
                    .start()
            }
            let temp = tween(this.node)
                .delay(0.1 + timer / 0.1)
                .call(() => {
                    rabbit.node.audio.playwheelspin();
                })
                .union()
                .repeatForever();
            temp.start();
        };
        let timeNum = 0;
        if (!rabbit.slotCtrl.isSpeed) {
            if (this.wheelId == 1) {
                timeNum += 0.1;
            }
            if (this.wheelId == 2) {
                timeNum += 0.4;
            }
        }
        setTimeout(() => {
            this.node.y = this.p; //滚轮启动位移距离 
            let timer = TIMEMIN;
            if (rabbit.slotCtrl.isSpeed) {
                timer = timer / 2;
            } else {
                timer = TIMEMIN + this.wheelId * 0.2;
            }
            tween(this.node)
                .delay(timeNum)
                .to(timer, { position: new Vec3(this.node.x, -this.node.h + 672 + this.p) })
                .call(() => {
                    loopCb();
                })
                .start();
            let temp = tween(this.node)
                .delay(0.1 + timer / 0.1)
                .call(() => {
                    rabbit.node.audio.playwheelspin();
                })
                .union()
                .repeatForever();
            temp.start();
        }, 100);
        this.nextCb = loopCb;
    }

    getRandomId() {
        let randomId = 0;
        while (randomId == 0) {
            randomId = Math.floor(Math.random() * (rabbit.node.rolePb.length - 1) + 1);
            if (randomId > 8 || randomId == 7) {
                randomId = 0;
            }
            randomId = randomId == this.excludeId ? 0 : randomId;
        };
        return randomId;
    }

    addRole(id: any, num: any = 0, posId: any = undefined) {
        if (id == 10) {
            this.addNullRole(10);
            return;
        }
        let pb = instantiate(rabbit.node.rolePb[id]);
        let pbNum: pbCardNum = pb.getComponent(pbCardNum) as pbCardNum;
        if (num) {
            pbNum.val = num * rabbit.betSum / rabbit.exchangeRate;
            if (num <= 10) {
                pb.children[1].active = true;
                pbNum.showNodeNum = 1;
            } else if (num <= 100) {
                pb.children[2].active = true;
                pbNum.showNodeNum = 2;
            } else if (num <= 5000) {
                pb.children[3].active = true;
                pbNum.showNodeNum = 3;
            }

            ((pb.getChildByName("001") as Node).getComponent(Label) as Label).string = Num.exchangeToThousands2(rabbit.exchangeRate, num * rabbit.betSum);
            ((pb.getChildByName("002") as Node).getComponent(Label) as Label).string = Num.exchangeToThousands2(rabbit.exchangeRate, num * rabbit.betSum);
            ((pb.getChildByName("003") as Node).getComponent(Label) as Label).string = Num.exchangeToThousands2(rabbit.exchangeRate, num * rabbit.betSum);
        }
        if (posId !== undefined) {
            this.rolePbList.unshift(pb);
            this.roleIdList.unshift(id);
        } else {
            this.rolePbList.push(pb);
            this.roleIdList.push(id);
        }
        this.addClickEvent(pb, id);
        this.node.addChild(pb);
        if (posId !== undefined) {
            pb.setSiblingIndex(posId);
        }
    }

    initAddRole(id: any, num: any, posId: any = undefined) {
        if (id == 10) {
            this.addNullRole(10);
            return;
        }
        let pb = instantiate(rabbit.node.rolePb[id]);
        let pbNum: pbCardNum = pb.getComponent(pbCardNum) as pbCardNum;
        if (num) {
            pbNum.val = num / rabbit.exchangeRate;
            if (num <= 25) {
                pb.children[1].active = true;
                pbNum.showNodeNum = 1;
            } else if (num <= 100) {
                pb.children[2].active = true;
                pbNum.showNodeNum = 2;
            } else if (num <= 2500) {
                pb.children[3].active = true;
                pbNum.showNodeNum = 3;
            }
            ((pb.children[1] as Node).getComponent(Label) as Label).string = this.initFormatNum(num)
                ((pb.children[2] as Node).getComponent(Label) as Label).string = this.initFormatNum(num)
                    ((pb.children[3] as Node).getComponent(Label) as Label).string = this.initFormatNum(num)
        }
        if (posId !== undefined) {
            this.rolePbList.unshift(pb);
            this.roleIdList.unshift(id);
        } else {
            this.rolePbList.push(pb);
            this.roleIdList.push(id);
        }
        this.addClickEvent(pb, id);
        this.node.addChild(pb);
        if (posId !== undefined) {
            pb.setSiblingIndex(posId);
        }
    }

    initFormatNum(num: any) {
        if (Number.isInteger(num)) {
            return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        } else {
            return num.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        }
    }

    addNullRole(id: any) {
        let pb = instantiate(rabbit.node.roleNullPb);
        this.rolePbList.push(pb);
        this.roleIdList.push(id);
        this.node.addChild(pb);
    }

    rollCallBack() {
        rabbit.node.audio.playStopWheel();
        this.lastResult = this.roleIdList.slice(0, 5);
        for (let i = 0; i < this.lastResult.length; i++) {
            if (this.lastResult[i] == 8) {
                let pbNum: pbCardNum = this.rolePbList[i].getComponent(pbCardNum) as pbCardNum;
                this.lastResultNum[i] = parseFloat(pbNum.val + "");
            } else {
                this.lastResultNum[i] = 0;
            }
        }
        this.status = 0;
        rabbit.node.stateCallBack();
    }

    stopImmediately() {
        Tween.stopAllByTarget(this.node);
        this.lastResult = this.roleIdList.slice(0, 5);
        for (let i = 0; i < this.lastResult.length; i++) {
            if (this.lastResult[i] == 8) {
                this.lastResultNum[i] = parseFloat(this.rolePbList[i].children[1].getComponent(Label).string);
            } else {
                this.lastResultNum[i] = 0;
            }
        }
        setTimeout(() => {
            this.setBlur(false);
            this.node.y = -this.node.h + 692 + this.p;
        }, 50);
        this.status = 0;
        rabbit.node.stateCallBack();
    }

    addClickEvent(node: any, idx: any) {
        let clickEventHandler = new Component.EventHandler();
        clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点 
        clickEventHandler.component = "rabbitWheel";// 这个是代码文件名 
        clickEventHandler.handler = "roleClick";
        clickEventHandler.customEventData = idx;
        let button = node.getComponent(Button);
        button.clickEvents.push(clickEventHandler);
    }

    roleClick(event: any, customEventData: any) {
        if (rabbit.detail) {
            rabbit.node.deskMask.active = false;
            rabbit.detail.node.parent = null;
            rabbit.detail = null;
            return;
        }

        let newNode = instantiate(rabbit.node.detailPb);
        let transForm: UITransform = (event.target as Node).getComponent(UITransform) as UITransform;
        let newVec3: Vec3 = transForm.convertToWorldSpaceAR(new Vec3(0, 0, 0));
        transForm = rabbit.node.getComponent(UITransform) as UITransform;
        let p = transForm.convertToNodeSpaceAR(newVec3);
        newNode.position = p;
        newNode.x -= 50;
        let isLeft = newNode.x < 10;
        (newNode.getComponent("rabbitDetail") as rabbitDetail).init(Number(customEventData), isLeft, event.target);
        rabbit.node.node.addChild(newNode);
        rabbit.node.deskMask.active = true;
    }

    setBlur(state: any) {
        for (let i in this.rolePbList) {
            if (this.roleIdList[i] != 8) {
                this.rolePbList[i].children[0].active = state;//虚图 
                this.rolePbList[i].children[1].active = !state;//实图 
            }
        }
    }

}



